import React from "react";
import { useState } from "react";
import { MathUtils, DoubleSide } from "three";

import { SceneProps } from "./scene_props";

const degToRad = MathUtils.degToRad

export const Plane = ({color, size, elevation = 0, depthTest = true, depthWrite = false, wireframes = false}: SceneProps) => {
    return <mesh scale={[200, 200, 1]} rotation={[degToRad(-90), 0, 0]} position={[0, elevation, 0,]}>
        <planeGeometry attach="geometry" args={[1, 1, size, size]} />
        <meshPhongMaterial attach="material" color={color} side={DoubleSide} depthTest={depthTest} depthWrite={depthWrite} wireframes={wireframes} />
    </mesh>
};
// alias
export const PlainPlane = Plane;

export const PlaneWithWireframes = ({color, size, elevation = 0, depthTest = true, depthWrite = false, wireframes = false}: SceneProps) => {
    const [showWireframes, setShowWireframes] = useState(wireframes);

    return <mesh scale={[size, size, 1]} rotation={[degToRad(-90), 0, 0]} position={[0, elevation, 0,]} onClick={()=>setShowWireframes(!showWireframes)}>
        <planeGeometry attach="geometry" args={[1, 1, size, size]} />
        <meshPhongMaterial attach="material" color={color || '#ffffff'} side={DoubleSide} depthTest={depthTest} depthWrite={depthWrite} wireframes={showWireframes} />
    </mesh>
};
