import React, { useRef } from "react";
import { Color, Fog } from "three";

import { useFrame, useThree } from "react-three-fiber";

import { SceneProps } from "./scene_props"; 
import { TexturedPlane } from "./text";
import { Plane } from "./plane";

export const WhiteScene = (props: SceneProps) => {
    const gridHelper = useRef();
    
    useThree((t) => {
        if (!t.scene.background) t.scene.background = new Color(0xe0e0f0);
        if (!t.scene.fog) t.scene.fog = new Fog(0xe0e0e0, 20, 100);
        //console.log(t, gridHelper);
        // if (gridHelper.current) {
        //     const g: any = gridHelper.current;
        //     g.material.opacity = 0.2;
        //     g.material.transparent = true;
        // }
    });

    let gridHelperInitialized = false;
    useFrame(() => {
        if(!gridHelperInitialized && gridHelper.current) {
            gridHelperInitialized = true;
            console.log('gridHelperInitialized');
            const g: any = gridHelper.current;
            g.material.opacity = 0.2;
            g.material.transparent = true;
        }
    })

    return <>
        <ambientLight intensity={props.ambientLightIntensity || 0.1} />
        <hemisphereLight args={[0xffffff, 0x444444]} position={[0, 20, 0]} />
        <directionalLight args={[0xffffff]} position={[0, 20, 10]} />

        <Plane size={2000} color={'#999999'} depthWrite={false} />
        <gridHelper args={[200, 40, '#000000', '#000000']} ref={gridHelper} />
    </>
};

